import {
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    style,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base';

export const feedbackMessageStyles: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    const red = cssVariableAsVar('color_traffic_light_1');
    const graySeven = cssVariableAsVar('color_secondary_7');
    const grey = cssVariableAsVar('color_primary_3');

    return [
        style,
        unsafeCSS(`
        ${root} {
            text-align:left;
            text-decoration: none;
            text-transform: none;
            font-size: 14px;
            margin:0;
        }
        ${is('basic')} {
            color: ${grey};
        }
        ${is('failure')} {
            color: ${red};
        }
        ${is('success')}{
            color: ${graySeven}
        }
        `),
    ] as CSSResultArray;
};
