import { BaseElement, createClassName, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { FeedbackMessageType } from './feedbackMessageType';
import { feedbackMessageStyles } from './feedbackMessageStyle';
import { FeedbackMessageProperties } from './feedbackMessageProperties';

@defineElement({ tagName: 'feedback-message', styles: feedbackMessageStyles, version })
export class FeedbackMessage extends BaseElement implements FeedbackMessageProperties {
    // Private members

    // Protected members

    // Public members
    @property({ type: FeedbackMessageType, attribute: 'message-type' })
    feedbackType;

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLocaleLowerCase(), {
            failure: this.feedbackType === FeedbackMessageType.Failure,
            success: this.feedbackType === FeedbackMessageType.Success,
            basic: this.feedbackType === FeedbackMessageType.Basic,
        });

        return html`
            <p class="${rootClasses}">
                <span class="${baseClassName}">
                    <slot></slot>
                </span>
            </p>
        `;
    }
}
